import { Link } from "react-router-dom";

const Navbar = () => {
    return ( 
        <nav className="navbar">
            <h1>Nazanin Taghavi</h1>
            <div className="links">
                <Link to="/">Home</Link>
                <Link to="/contact">Contact</Link>
                <Link to="/create" style={{
                    color: "white",
                    backgroundColor: '#7d17b8',
                    borderRadius: '8px'
                }}>Resume</Link>

            </div>
        </nav>
     );
}
 
export default Navbar;