import { useState } from "react";
import { useHistory } from "react-router";

const Create = () => {
    const [title, setTitle] = useState('');
    const [password, setPassword] = useState('');
    const [body, setBody] = useState('');
    const [cls , setCls] = useState('AI');
    const [isPending, setIsPending] = useState(false);
    const redirect = useHistory();

    const handleSubmit = (e) =>{
        e.preventDefault(); // do not refresh
        const skill = {title,cls, body};

        setIsPending(true);
        fetch('http://localhost:3000/skills',{
            method: 'POST',
            headers: {"Content-Type":"application/json"},
            body: JSON.stringify(skill)
        })
        .then(()=> {
            console.log('new skill added');
            setIsPending(false);
           // redirect.go(-1);
           redirect.push('/');
        })

    }
    return ( 
        <div className="create">
            <h2>Add new Skills (admin mode)</h2>
            <form onSubmit={handleSubmit}>
                <label>Admin Password</label>
                <input type='password' required
                value={password} onChange={(e)=> setPassword(e.target.value)}/>

                <label>Skill Title:</label>
                <input type='text' required
                value={title} onChange={(e)=> setTitle(e.target.value)}/>

                <label>Skill Description:</label>
                <textarea required
                value={body} onChange={(e)=> setBody(e.target.value)}></textarea>

                <label>Skill Class:</label>
                <select value={cls} onChange={(e)=> setCls(e.target.value)}>
                    <option value="AI">AI</option>
                    <option value="Web Development">Web Development</option>
                </select>

                {!isPending && <button>Add Skill</button>}
                {isPending && <button>Adding Skill ...</button>}
            </form>
        </div>
     );
}
 
export default Create;